import { Avatar, Container } from "@mui/material";

export default function Massage(prop) {
  return (
    <div dir="rtl">
      <Container className="massage-form  ">
        <Avatar
        className="ml-3"
          alt="Avatar"
          src="https://mui.com//static/images/avatar/1.jpg"
        />
        <span >Mohammed :</span>
        <p>{prop.massageSand}</p>
      </Container>
    </div>
  );
}
